<template>
  <div class="blogTOC-container">
    <h2></h2>
    <ClassList :list="tocWithSelect" @select="handleSelect" />
  </div>
</template>

<script>
import ClassList from "./classList.vue";
import debounce from "@/utils/debounce";
export default {
  components: { ClassList },
  props: {
    blogTOC: { typeof: Array, required: true },
  },
  data() {
    return {
      activeAnchor: "", //(article-md-title-1)
    };
  },
  created() {
    this.debounceSetSelect = debounce(this.setSelect, 50);
    this.$bus.$on("mainScroll", this.debounceSetSelect);
  },
  destroyed() {
    this.$bus.$off("mainScroll", this.debounceSetSelect);
  },
  methods: {
    handleSelect(item) {
      location.hash = item.anchor;
    },
    //activeAnchor
    setSelect(scrollDom) {
      if (!scrollDom) {
        return;
      }
      this.activeAnchor = ""; //
      const range = 150; //
      for (const dom of this.getDoms) {
        //dom
        if (!dom) {
          continue; //
        }
        // console.log(dom.getBoundingClientRect().top);
        const top = dom.getBoundingClientRect().top;
        if (top > 0 && top <= range) {
          //
          this.activeAnchor = dom.id;
          return;
        } else if (top > range) {
          //()
          return;
        } else {
          //
          this.activeAnchor = dom.id; //,()
        }
      }
    },
  },
  computed: {
    //isSelecttoc
    tocWithSelect() {
      const getTOC = (TOC = []) => {
        return TOC.map((item) => ({
          ...item,
          isSelect: item.anchor === this.activeAnchor,
          children: getTOC(item.children),
        }));
      };
      return getTOC(this.blogTOC);
    },

    //toc
    getDoms() {
      const doms = [];

      const addToDoms = (toc) => {
        for (const item of toc) {
          const dom = document.getElementById(item.anchor);
          doms.push(dom);
          if (item.children && item.children.length) {
            addToDoms(item.children);
          }
        }
      };
      addToDoms(this.blogTOC);
      return doms;
    },
  },
};
</script>

<style scoped lang="less">
.blogTOC-container {
  h2 {
    font-weight: bold;
    font-size: 1em;
    margin: 0;
    letter-spacing: 2px;
  }
}
</style>