Mock.mock(/^\/api\/blog(\?.+)?$/,"get",function(options){
    const query = qs.parse(options.url);
    return Mock.mock({
        code:0,
        mes:'',
        data:{
            "total|2000-3000":0,
            [`rows|${query.limit || 10}`]:[
                {
                    id: "@guid",
                    title: "@ctitle(1,50)",
                    description: "@cparagraph(1, 10)",
                    category: {
                        "id|1-10": 0,
                        name: "@id",
                    },
                    "scanNumber|0-3000": 0,
                    "commentNumber|0-300": 30,
                    "thumb|1": ["@image(300x250, @color, #ffffff, @title)"],
                    createDate: `@date('T')`,
        },
            ],
        }
    })
})


