import getComponentRootDom from './getComponentRootDom';
import Icon from '@/components/Icon';
import styles from './message.module.less';

/**
 * 
 * @param {String} content 
 * @param {String} type  success error info warn
 * @param {Number} duration ()
 * @param {HTMLElement} container 
 * @param {Function} callBack 
 */
export default function(option={}){
    const content = option.content || '';
    const type =  option.type || 'error';
    const duration = option.duration || 2000;
    const container = option.container || document.body;
    const div = document.createElement('div');
    const iconDom = getComponentRootDom(Icon,{type,});
    div.className = `${styles.message} ${styles['message-'+type]}`
    div.innerHTML = `<span>${iconDom.outerHTML}</span>:<div>${content}</div>`;
 
    //
    if(option.container){//bodyrelative
        if(getComputedStyle(container).position==='static'){
            container.style.position = 'relative';
        }
    }
    
    container.appendChild(div);
    
    //
    div.clientHeight;//reflow

    //
    div.style.transform = 'translate(-50%,-50%)'
    div.style.opacity = 1;

    setTimeout(() => {
        div.style.transform = 'translate(-50%,-50%) translateY(-50px)'
        div.style.opacity = 0;
        div.addEventListener('transitionend',function(){
            div.remove()

            //
            option.callBack && option.callBack();
        },{once:true})
    }, duration);
}