import eventBus from '@/eventBus'
import debounce from '@/utils/debounce'
import defaultGif from '@/assets/common.gif'

//
let images = [];


// setInterval(() => {
//     console.log(images);
// }, 1000);

//
function handlerImage(img){
    //
    if(img.handler){
        return;
    }

    img.dom.src = defaultGif;//
    //
    //
    const clientHeight = document.documentElement.clientHeight;//
    const positionData = img.dom.getBoundingClientRect();//
    const imgHeight = positionData.height || 150;//
    if(positionData.top>-imgHeight && positionData.top<=clientHeight){
        //
        const tempImg = new Image();
        tempImg.onload = function(){
            img.dom.src = img.src;
        }
        tempImg.src = img.src;
        // img.dom.src = img.src;
        images = images.filter(i=>i !== img)//
        img.handler = true;//
    }
    
}

//
function setImages(){
    for (const img of images) {
        //
        handlerImage(img);
    }
}
function handlerScroll(dom){
    setImages();
}
eventBus.$on('mainScroll',debounce(handlerScroll,50));

export default {
    inserted(el,binding){
        const img = {
            dom:el,
            src:binding.value,
            handler:false
        }
        images.push(img)
        handlerImage(img)
    },
    unbind(el){
        images = images.filter(img=>img.dom!==el)
    }
} 